  border-radius: 5px;
  background-color: lightgray;
  cursor: pointer;
  outline: none;
}
.custom-select__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: 10px;
  background-color: #000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
